export const ADD_MENU = '/Menu/Overview/ADD_MENU';
export const ADD_MENU_SUCCESS = '/Menu/Overview/ADD_MENU_SUCCESS';

export const LOAD_MENU = '/Menu/Overview/LOAD_MENU';
export const LOAD_MENU_SUCCESS = '/Menu/Overview/LOAD_MENU_SUCCESS';

export const ADD = '/Menu/Overview/ADD';

export const FIELDS_CHANGE = '/Menu/Overview/FIELDS_CHANGE';

export const MODAL_VISIBLE_TOGGLE = '/Menu/Overview/MODAL_VISIBLE_TOGGLE';

export const UPDATE_MENU = '/Menu/Overview/UPDATE_MENU';

export function add(params) {
  return {
    type: ADD,
    params,
  };
}

export function addMenu(params) {
  return {
    type: ADD_MENU,
    params,
  };
}

export function addMenuSuccess(data) {
  return {
    type: ADD_MENU_SUCCESS,
    data,
  };
}

export function loadMenu(params) {
  return {
    type: LOAD_MENU,
    params,
  };
}

export function loadMenuSuccess(data) {
  return {
    type: LOAD_MENU_SUCCESS,
    data,
  };
}

export function fieldsChange(fields, editId) {
  return {
    type: FIELDS_CHANGE,
    fields,
    editId,
  }
}

export function modalVisibleToggle(bool, editId) {
  return {
    type: MODAL_VISIBLE_TOGGLE,
    bool,
    editId,
  }
}

export function updateMenu(params) {
  return {
    type: UPDATE_MENU,
    params,
  }
}

